import React from 'react';
import { Canvas } from '@react-three/fiber';
import { useGLTF, OrbitControls, PresentationControls, Stage } from '@react-three/drei';

// Function to load GLTF model
function TruckModel() {
  const { scene } = useGLTF('/3DObject/shiba.glb');
  return <primitive object={scene} />;  
}
function BoxModel() {
  const { scene } = useGLTF('box.glb');
  return <primitive object={scene} />;
}
function ShibaModel() {
  const { scene } = useGLTF('shiba.glb');
  return <primitive object={scene} />;
}
function Model(props) {
  const { scene } = useGLTF("/3DObject/box.glb");
  return <primitive object={scene} {...props} />
}

// Main Scene component
function Scene() {
  return (
    <>
    <div className='row'>
      <div className='col-8'>
        <Canvas dpr={[1,2]} shadows camera={{ fov: 45 }} style={{"position": "absolute"}}>
          <color attach="background" args={["#101010"]} />
          <PresentationControls speed={1.5} global zoom={.5} polar={[-0.1, Math.PI / 4]}>
            <Stage environment={"sunset"}>
              <Model scale={0.01} />
            </Stage>
          </PresentationControls>
        </Canvas>
      </div>
      {/* <div className='col-4'>
        <Canvas camera={{ position: [-10, 5, 0], fov: 75 }}>
          <ambientLight intensity={0.5} />
          <spotLight position={[10, 10, 10]} angle={0.15} penumbra={1} />
          <TruckModel />
          <OrbitControls />
        </Canvas>
      </div> */}
    </div>       
    </>
  );
}

export default Scene;
